*{margin:0;padding:0}html,body{height:100vh}.App{font-family:sans-serif;text-align:center;margin:0;padding:0;width:100%;height:100vh;background:#DD9990}.control{cursor:pointer}.control:hover .circle-click{background:#deff8a}.circle,.circle-click,.circle-speaking{width:80px;height:80px;position:absolute;left:-40px;right:96.25%;top:12vh;border-radius:50px;cursor:pointer;border:0;background-color:#CD1515}.circle-click{border:0px;background-color:#fff}.circle-speaking{border:0px;background-color:#fff}.status,.status-talk{position:absolute;width:100vw;left:20px;right:96.25%;top:6vh;font-family:IBM Plex Mono, monospace;font-style:italic;font-weight:light;font-size:5vh;line-height:px;text-indent:10px;text-align:left;color:#063746;z-index:1}.status-talk{font-weight:bold}.glow-on-hover{width:220px;height:50px;cursor:pointer;position:relative;z-index:0;border-radius:10px;content:'';background:linear-gradient(45deg, #002bff, #7a00ff, #ff00c8, red, #ff7300, #ff00c8, #7a00ff, #002bff);top:-2px;left:-2px;background-size:400%;opacity:1;color:#fff;font-size:2rem;border:none;outline:none}@-webkit-keyframes glowing{0%{background-position:0 0}50%{background-position:400% 0}100%{background-position:0 0}}@keyframes glowing{0%{background-position:0 0}50%{background-position:400% 0}100%{background-position:0 0}}@-webkit-keyframes bordering{0%{box-shadow:0 0 0px #deff8a}50%{box-shadow:0 0 0px 12vh #deff8a}100%{box-shadow:0 0 0px #deff8a}}@keyframes bordering{0%{box-shadow:0 0 0px #deff8a}50%{box-shadow:0 0 0px 12vh #deff8a}100%{box-shadow:0 0 0px #deff8a}}@-webkit-keyframes bordersize{0%{box-shadow:0 0 0px #fff}50%{box-shadow:0 0 0px 12vh #fff}100%{box-shadow:0 0 0px #fff}}@keyframes bordersize{0%{box-shadow:0 0 0px #fff}50%{box-shadow:0 0 0px 12vh #fff}100%{box-shadow:0 0 0px #fff}}[data-xstate]{justify-self:stretch;align-self:stretch;border:none}

